import React from 'react';
import { Navigate } from 'react-router-dom';

export const PrivateRoute = ({ children }) => {
    if (sessionStorage.getItem("accesstoken")===null||sessionStorage.getItem("accesstoken")===undefined) {
        return <Navigate to="/" />;
    }
    else{
    return (
        <>
            {children}
                
        </>
    );
    }
};