export const actionExportDfuTable = (body, callback) => {
  return async (dispatch) => {
    try {
      const URL = URLConfig.exportDFUTable();
      const res = await axios.post(URL, body);
      if (res.data) {
        callback(res.data.msg);
      }
    } catch (err) {}
  };
};

