import React, {
  Fragment,
  // DfuDropDownComponent,
  useImperativeHandle,
  useRef,
  useState,
  useEffect,
} from "react";
import { useMemo } from "react";
import { Form } from "react-bootstrap";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import {
  actionDfuTableDrpDwnFilter,
  actionGetTableData,
} from "../../../redux/Actions/QuickFilterData";

export  const DfuDropDownComponent = (props, ref) => {
  const dispatch = useDispatch();
  const [triggerState, settriggerState] = useState(false);
  const { tableData, plotData, tableDataRequest, tableFiterdata, state } =
    useSelector(
      (state) => ({
        tableData: state.quickFilterData.tableData,
        plotData: state.plotData.plotData,
        tableDataRequest: state.quickFilterData.tableDataRequest,
        tableFiterdata: state.quickFilterData.tableDropDownFilters,
        state: state,
      }),
      shallowEqual
    );
  useEffect(() => {
    if (triggerState === true) {
      // Object.assign(tableDataRequest.filter, tableFiterdata);
      // dispatch(actionGetTableData(tableDataRequest));
      dispatch(actionGetTableData());
    }
    settriggerState(false);
  }, [triggerState]);
  const { column } = props;
  const uniqueDfuRecords = useMemo(() => {
    const temp = tableData.map((dfuData) => dfuData[column.colDef.field]);
    return temp.filter((dfuData, i) => dfuData && temp.indexOf(dfuData) === i);
    // .map((e) => ({ value: e, label: e }));
  }, []);

  const handleClick = (e) => {
    e.preventDefault();
    var ob = {};
    if (column.colDef.headerName === "Material Name") {
      ob = {
        material_name: e.target.value,
      };
    } else if (column.colDef.headerName === "Customer Name") {
      ob = {
        customer_name: e.target.value,
      };
    } else if (column.colDef.headerName === "Ship to name") {
      ob = {
        ship_to_name: e.target.value,
      };
    } else if (column.colDef.headerName === "Del new") {
      ob = {
        del_new: e.target.value,
      };
    } else if (column.colDef.headerName === "Sales rep name") {
      ob = {
        sales_rep_name: e.target.value,
      };
    } else {
      ob = {
        [column.colDef.headerName]: e.target.value,
      };
    }
    dispatch(actionDfuTableDrpDwnFilter(ob));
    settriggerState(true);
  };

  return (
    <Fragment>
      {/* <input style={style} type="text" min="0" onInput={onInputBoxChanged} /> */}
      <Form.Control
        className="table-drop-select"
        as={"select"}
        aria-label="Select"
        size="sm"
        onChange={handleClick}
        title={"Select " + column.colDef.headerName}
      >
        <option id="drop-options">Select {column.colDef.headerName} </option>
        {uniqueDfuRecords.map((option, index) => (
          <option id="drop-options" key={index} value={option}>
            {/* <Form.Check type="checkbox" label={option} /> */}
            {option}
          </option>
        ))}
      </Form.Control>
    </Fragment>
  );
};
export default DfuDropDownComponent;